.drawer {
	position: absolute;
	display: flex;
	flex-direction: column;
	width: 420px;
	height: 100%;
	right: 0;
	background: #ffffff;
	box-shadow: -10px 4px 24px rgba(0, 0, 0, 0.1);
	padding: 30px;
	transform: translateX(100%);
	transition: transform 0.3s ease-in-out;

	h2 {
		margin: 0;
	}

	.cartEmpty {
		text-align: center;

		p {
			width: 280px;
			line-height: 24px;
		}

		.greenButton {
			width: 245px;
			margin-top: 20px;

			&:hover {
				img {
					transform: rotate(180deg) translateX(3px);
				}
			}

			img {
				position: relative;
				top: 1px;
				transform: rotate(180deg);
				margin-right: 15px;
				transition: transform 0.15s ease-in-out;
			}
		}
	}

	.itemsWrapper {
		overflow: auto;
	}

	.items {
		flex: 1;
		overflow: auto;
		margin-bottom: 40px;
	}

	.cartItem {
		background: #ffffff;
		border: 1px solid #f3f3f3;
		border-radius: 20px;
		padding: 20px;
		overflow: hidden;

		p {
			margin: 0;
			font-size: 16px;
		}

		b {
			font-size: 14px;
		}

		.cartItemImg {
			width: 70px;
			height: 70px;
			background-size: contain;
			background-repeat: no-repeat;
			background-position: 0 -3px;
			margin-right: 20px;
		}

		.removeBtn {
			opacity: 0.5;
			transition: opacity ease-in-out 0.3s;

			&:hover {
				opacity: 1;
			}
		}
	}

	.cartTotalBlock {
		ul {
			margin-bottom: 40px;

			li {
				display: flex;
				align-items: flex-end;
				margin-bottom: 20px;
			}

			div {
				flex: 1;
				height: 1px;
				border-bottom: 1px dashed #dfdfdf;
				position: relative;
				top: -4px;
				margin: 0 7px;
			}
		}

		.greenButton {
			position: relative;

			&:disabled {
				animation: button-loading 0.6s ease-in-out infinite;
			}

			&:hover {
				img {
					transform: translateX(15px);
				}
			}

			img {
				position: absolute;
				right: 70px;
				top: 23px;
				transition: transform ease-in-out 0.2s;
			}
		}
	}

	.greenButton {
		width: 100%;
		height: 55px;
		background: #9dd558;
		color: #ffffff;
		font-size: 16px;
		font-weight: 500;
		border-radius: 18px;
		transition: background ease-in-out 0.2s;

		&:disabled {
			background-color: #bebebe !important;
			cursor: default;
		}

		&:hover {
			background: lighten($color: #9dd558, $amount: 5%);
		}

		&:active {
			background: darken($color: #9dd558, $amount: 5%);
		}
	}
}

.overlay {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.5);
	z-index: 1;
	opacity: 0;
	visibility: hidden;
	transition: opacity 0.1s ease-in-out, visibility 0.1s ease-in-out;
	overflow: hidden;
}

.overlayVisible {
	visibility: visible;
	opacity: 1;

	.drawer {
		transform: translateX(0);
	}
}
